<template>
	<view class="login-container">
		<!-- È°∂ÈÉ®Áä∂ÊÄÅÊ†èÂç†‰Ωç -->
		<view class="status-bar" :style="{height: statusBarHeight + 'px'}"></view>
		
		<!-- ÁôªÂΩïÂÜÖÂÆπ -->
		<view class="login-content">
			<!-- LogoÂíåÊ†áÈ¢ò -->
			<view class="logo-section">
				<view class="logo">üìñ</view>
				<text class="title">ÁÆÄÂ∫êÊó•ËÆ∞</text>
				<text class="subtitle">{{ isLogin ? 'ËÆ∞ÂΩïÁîüÊ¥ªÔºåÂàÜ‰∫´ÁæéÂ•Ω' : 'ÂºÄÂßãÊÇ®ÁöÑÊó•ËÆ∞‰πãÊóÖ' }}</text>
			</view>
			
			<!-- ÁôªÂΩïË°®Âçï -->
			<view class="form-section" v-if="isLogin">
				<view class="form-item">
					<input 
						class="input" 
						type="text" 
						placeholder="ËØ∑ËæìÂÖ•Áî®Êà∑Âêç"
						v-model="loginForm.username"
						:disabled="loading"
					/>
				</view>
				
				<view class="form-item">
					<input 
						class="input" 
						:type="showPassword ? 'text' : 'password'"
						placeholder="ËØ∑ËæìÂÖ•ÂØÜÁ†Å"
						v-model="loginForm.password"
						:disabled="loading"
					/>
					<view class="password-toggle" @tap="togglePassword">
						{{ showPassword ? 'üëÅÔ∏è' : 'üëÅÔ∏è‚Äçüó®Ô∏è' }}
					</view>
				</view>
				
				<button 
					class="btn btn-primary login-btn" 
					@tap="handleLogin"
					:disabled="loading"
				>
					{{ loading ? 'ÁôªÂΩï‰∏≠...' : 'ÁôªÂΩï' }}
				</button>
				
				<!-- ÂæÆ‰ø°ÁôªÂΩïÊåâÈíÆ -->
				<view class="wechat-login-section">
					<view class="divider">
						<text class="divider-text">Êàñ</text>
					</view>
					<button 
						class="btn btn-wechat login-btn" 
						@tap="handleWechatLogin"
						:disabled="loading"
					>
						<text class="wechat-icon">üì±</text>
						{{ loading ? 'ÁôªÂΩï‰∏≠...' : 'ÂæÆ‰ø°ÁôªÂΩï' }}
					</button>
				</view>
			</view>
			
			<!-- Ê≥®ÂÜåË°®Âçï -->
			<view class="form-section" v-else>
				<view class="form-item">
					<input 
						class="input" 
						type="text" 
						placeholder="ËØ∑ËæìÂÖ•Áî®Êà∑Âêç"
						v-model="registerForm.username"
						:disabled="loading"
					/>
				</view>
				
				<view class="form-item">
					<input 
						class="input" 
						type="text" 
						placeholder="ËØ∑ËæìÂÖ•ÈÇÆÁÆ±"
						v-model="registerForm.email"
						:disabled="loading"
					/>
				</view>
				
				<view class="form-item">
					<input 
						class="input" 
						type="text" 
						placeholder="ËØ∑ËæìÂÖ•ÊòµÁß∞ÔºàÂèØÈÄâÔºâ"
						v-model="registerForm.nickname"
						:disabled="loading"
					/>
				</view>
				
				<view class="form-item">
					<input 
						class="input" 
						type="password" 
						placeholder="ËØ∑ËæìÂÖ•ÂØÜÁ†Å"
						v-model="registerForm.password"
						:disabled="loading"
					/>
				</view>
				
				<view class="form-item">
					<input 
						class="input" 
						type="password" 
						placeholder="ËØ∑ÂÜçÊ¨°ËæìÂÖ•ÂØÜÁ†Å"
						v-model="registerForm.confirmPassword"
						:disabled="loading"
					/>
				</view>
				
				<button 
					class="btn btn-primary login-btn" 
					@tap="handleRegister"
					:disabled="loading"
				>
					{{ loading ? 'Ê≥®ÂÜå‰∏≠...' : 'Ê≥®ÂÜå' }}
				</button>
			</view>
			
			<!-- ÂàáÊç¢ÁôªÂΩï/Ê≥®ÂÜå -->
			<view class="switch-section">
				<text class="switch-text" @tap="toggleMode">
					{{ isLogin ? 'Ê≤°ÊúâË¥¶Êà∑ÔºüÁ´ãÂç≥Ê≥®ÂÜå' : 'Â∑≤ÊúâË¥¶Êà∑ÔºüÁ´ãÂç≥ÁôªÂΩï' }}
				</text>
			</view>
			
			<!-- ÊµãËØïË¥¶Êà∑ÊèêÁ§∫ -->
			<view class="test-account">
				<text class="test-title">ÊµãËØïË¥¶Êà∑Ôºö</text>
				<text class="test-info">ÁÆ°ÁêÜÂëò: admin / admin123</text>
				<text class="test-info">ÊµãËØïÁî®Êà∑: testuser / testpass123</text>
			</view>
		</view>
	</view>
</template>

<script setup>
	import { ref, reactive, onMounted } from 'vue'
	import { authApi } from '../../api/index.js'
	import { showSuccess, showError } from '../../utils/index.js'

	// ÂìçÂ∫îÂºèÊï∞ÊçÆ
	const statusBarHeight = ref(0)
	const isLogin = ref(true)
	const showPassword = ref(false)
	const loading = ref(false)

	const loginForm = reactive({
		username: '',
		password: ''
	})

	const registerForm = reactive({
		username: '',
		email: '',
		nickname: '',
		password: '',
		confirmPassword: ''
	})

	// È°µÈù¢Âä†ËΩΩÊó∂ÊâßË°å
	onMounted(() => {
		// Ëé∑ÂèñÁä∂ÊÄÅÊ†èÈ´òÂ∫¶
		const systemInfo = uni.getSystemInfoSync()
		statusBarHeight.value = systemInfo.statusBarHeight
	})
	// ÊñπÊ≥ïÂÆö‰πâ
	const toggleMode = () => {
		isLogin.value = !isLogin.value
		clearForms()
	}

	const togglePassword = () => {
		showPassword.value = !showPassword.value
	}

	const clearForms = () => {
		Object.assign(loginForm, {
			username: '',
			password: ''
		})
		Object.assign(registerForm, {
			username: '',
			email: '',
			nickname: '',
			password: '',
			confirmPassword: ''
		})
	}
			
	const handleLogin = async () => {
		if (!loginForm.username || !loginForm.password) {
			showError('ËØ∑Â°´ÂÜôÁî®Êà∑ÂêçÂíåÂØÜÁ†Å')
			return
		}

		loading.value = true
		console.log('ÂºÄÂßãÁôªÂΩïÔºåÁî®Êà∑Âêç:', loginForm.username)

		try {
			console.log('ÂèëÈÄÅÁôªÂΩïËØ∑Ê±ÇÂà∞:', 'http://localhost:3458/api/auth/login')
			console.log('ÁôªÂΩïÊï∞ÊçÆ:', loginForm)

			const response = await authApi.login(loginForm)
			console.log('ÁôªÂΩïÂìçÂ∫î:', response)

			if (response.success) {
				// ‰øùÂ≠òtokenÂíåÁî®Êà∑‰ø°ÊÅØ
				uni.setStorageSync('token', response.data.token)
				uni.setStorageSync('userInfo', response.data.user)

				console.log('ÁôªÂΩïÊàêÂäüÔºåtokenÂ∑≤‰øùÂ≠ò')
				showSuccess('ÁôªÂΩïÊàêÂäü')

				// Ë∑≥ËΩ¨Âà∞È¶ñÈ°µ
				setTimeout(() => {
					uni.switchTab({
						url: '/pages/home/home'
					})
				}, 1000)
			} else {
				console.error('ÁôªÂΩïÂ§±Ë¥•ÔºåÂìçÂ∫î:', response)
				showError(response.message || 'ÁôªÂΩïÂ§±Ë¥•')
			}
		} catch (error) {
			console.error('ÁôªÂΩïÂºÇÂ∏∏:', error)
			showError(`ÁôªÂΩïÂ§±Ë¥•: ${error.message || 'ÁΩëÁªúÈîôËØØ'}`)
		} finally {
			loading.value = false
		}
	}
			
	const handleRegister = async () => {
		const { username, email, password, confirmPassword, nickname } = registerForm

		if (!username || !email || !password) {
			showError('ËØ∑Â°´ÂÜôÂøÖË¶Å‰ø°ÊÅØ')
			return
		}

		if (password !== confirmPassword) {
			showError('‰∏§Ê¨°ËæìÂÖ•ÁöÑÂØÜÁ†Å‰∏ç‰∏ÄËá¥')
			return
		}

		loading.value = true
		try {
			const response = await authApi.register({
				username,
				email,
				password,
				nickname
			})

			if (response.success) {
				// ‰øùÂ≠òtokenÂíåÁî®Êà∑‰ø°ÊÅØ
				uni.setStorageSync('token', response.data.token)
				uni.setStorageSync('userInfo', response.data.user)

				showSuccess('Ê≥®ÂÜåÊàêÂäü')

				// Ë∑≥ËΩ¨Âà∞È¶ñÈ°µ
				setTimeout(() => {
					uni.switchTab({
						url: '/pages/home/home'
					})
				}, 1000)
			} else {
				showError(response.message || 'Ê≥®ÂÜåÂ§±Ë¥•')
			}
		} catch (error) {
			showError(error.message || 'Ê≥®ÂÜåÂ§±Ë¥•')
		} finally {
			loading.value = false
		}
	}

	// ÂæÆ‰ø°ÁôªÂΩïÂ§ÑÁêÜ
	const handleWechatLogin = async () => {
		loading.value = true
		try {
			console.log('ÂºÄÂßãÂæÆ‰ø°ÁôªÂΩï')

			// Ê£ÄÊü•ÁΩëÁªúÁä∂ÊÄÅ
			const networkType = await uni.getNetworkType()
			if (networkType.networkType === 'none') {
				throw new Error('ÁΩëÁªú‰∏çÂèØÁî®ÔºåËØ∑Ê£ÄÊü•ÁΩëÁªúËøûÊé•')
			}
			
			// 1. Ëé∑ÂèñÂæÆ‰ø°ÁôªÂΩïcode
			const loginResult = await uni.login({
				provider: 'weixin',
				onlyAuthorize: true,
				timeout: 10000 // 10ÁßíË∂ÖÊó∂
			})

			if (loginResult.errMsg !== 'login:ok') {
				throw new Error('ÂæÆ‰ø°ÁôªÂΩïÊéàÊùÉÂ§±Ë¥•')
			}

			const { code } = loginResult
			console.log('Ëé∑ÂèñÂæÆ‰ø°ÁôªÂΩïcodeÊàêÂäü:', code)

			// 2. ÂèëÈÄÅcodeÂà∞ÂêéÁ´ØËøõË°åÂæÆ‰ø°ÁôªÂΩï
			const response = await authApi.wechatLogin({ code })
			console.log('ÂæÆ‰ø°ÁôªÂΩïÂìçÂ∫î:', response)

			if (response.success) {
				// ‰øùÂ≠òtokenÂíåÁî®Êà∑‰ø°ÊÅØ
				uni.setStorageSync('token', response.data.token)
				uni.setStorageSync('userInfo', response.data.user)

				console.log('ÂæÆ‰ø°ÁôªÂΩïÊàêÂäüÔºåtokenÂ∑≤‰øùÂ≠ò')
				showSuccess('ÁôªÂΩïÊàêÂäü')

				// Ë∑≥ËΩ¨Âà∞È¶ñÈ°µ
				setTimeout(() => {
					uni.switchTab({
						url: '/pages/home/home'
					})
				}, 1000)
			} else {
				console.error('ÂæÆ‰ø°ÁôªÂΩïÂ§±Ë¥•ÔºåÂìçÂ∫î:', response)
				showError(response.message || 'ÂæÆ‰ø°ÁôªÂΩïÂ§±Ë¥•')
			}
		} catch (error) {
			console.error('ÂæÆ‰ø°ÁôªÂΩïÂºÇÂ∏∏:', error)
			
			// Â§ÑÁêÜÂêÑÁßçÈîôËØØÊÉÖÂÜµ
			if (error.errMsg) {
				if (error.errMsg.includes('auth deny')) {
					showError('ËØ∑ÊéàÊùÉÂæÆ‰ø°ÁôªÂΩï‰ª•ÁªßÁª≠‰ΩøÁî®')
				} else if (error.errMsg.includes('timeout')) {
					showError('ÂæÆ‰ø°ÁôªÂΩïË∂ÖÊó∂ÔºåËØ∑ÈáçËØï')
				} else if (error.errMsg.includes('network')) {
					showError('ÁΩëÁªúËøûÊé•Â§±Ë¥•ÔºåËØ∑Ê£ÄÊü•ÁΩëÁªúËÆæÁΩÆ')
				} else {
					showError(`ÂæÆ‰ø°ÁôªÂΩïÂ§±Ë¥•: ${error.errMsg}`)
				}
			} else if (error.message) {
				if (error.message.includes('ÁΩëÁªú')) {
					showError('ÁΩëÁªúËøûÊé•Â§±Ë¥•ÔºåËØ∑Ê£ÄÊü•ÁΩëÁªúËÆæÁΩÆ')
				} else {
					showError(error.message)
				}
			} else {
				showError('ÂæÆ‰ø°ÁôªÂΩïÂ§±Ë¥•ÔºåËØ∑ÈáçËØï')
			}
		} finally {
			loading.value = false
		}
	}
</script>

